.card {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    transition: 0.3s;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    background-color: #fff;
  }
  
  .card .cardHeader {
    display: flex;
    justify-content: space-between;
  }
  
  .card .cardHeader .cardTitle {
    color: #aaa;
    font-weight: 400;
    font-size: 17px;
  }
  
  .card .cardSection {
    margin-top: 15px;
  }
  
  .card .cardSection .cardPara {
    font-size: 17px;
    font-weight: 500;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .card .cardFooter {
    margin-top: 15px;
    display: flex;
    align-items: center;
  }
  
  .card .cardFooter .info {
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .card .cardFooter .tag {
    border: 1px solid #ddd;
    width: 100%;
    text-align: center;
    padding: 5px;
    border-radius: 5px;
    color: #aaa;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10px;
  }
  
  .card .cardFooter .tag .tagIcon {
    border-radius: 50%;
    height: 15px;
    width: 15px;
    background-color: #aaa;
  }

  .card .cardFooter p {
    margin-left: 5px;
  }